import React, { useState } from 'react'

import './modal.css'

import styled from 'styled-components'
import axios from 'axios'

const Login = styled.a`
border: 1px solid #f9f9f9;
padding 8px 2rem;
border-radius:4px;
letter-spacing:1.5px;
text-transform:uppercase;
background-color:rgba(0,0,0,0.6);
transition:all 0.2s ease 0.2s;
cursor:pointer;
color:white;
text-decoration:none;

&:hover{
    background-color:white;
    color:#000 !important;
    border-color:transparent;
}
`

function LoginModal() {
    const [username, setUsername] = useState('admin')
    const [password, setPassword] = useState('123')


    const handleSubmit = (e) => {
        e.preventDefault()
        if (username.length > 0 && password.length > 0) {
            axios.post('/api/login/', {
                username,
                password
            }).then(res => {
                if (res.data.valid) {
                    localStorage.setItem('userData', JSON.stringify(res.data.userData))
                    window.location.href = '/'
                } else {
                    alert('invalid credentials')
                }
            })
        }
    }
    return (
        <div>
            <aside id="login" class="modal">
                <div>
                    <LoginTitle>Login, Username: admin: Password:123</LoginTitle>
                    <a id='closeIcon' href="#close" title="Close">Close</a>
                    <Form>
                        <FormGroup>
                            <label>Enter Username</label>
                            <input onChange={(e) => {
                                setUsername(e.target.value)
                            }} value={username} type='text' required />
                        </FormGroup>
                        <FormGroup>
                            <label>Enter Password</label>
                            <input onChange={(e) => {
                                setPassword(e.target.value)
                            }} value={password} type='password' required />
                        </FormGroup>
                        <SubmitWrapper>
                            <LoginButton onClick={handleSubmit}>Login</LoginButton>
                        </SubmitWrapper>
                    </Form>
                </div>
            </aside>
            <Login href="#login" class="openModal">Login</Login>
        </div>
    )
}

export default LoginModal

const LoginTitle = styled.div`
color:black;
text-align:center;
margin: 2rem 0rem;
font-size:1.2rem;
`

const Form = styled.form`
padding:1rem 2rem;
background:white;
display:flex;
flex-direction:column;
border-radius:20px;
`

const FormGroup = styled.div`
display:flex;
flex-direction:column;
input{
padding:0.8rem 0rem;
border-radius:4px;
}

label{
    color:black;
    padding: 1rem 0rem;
}
`

const SubmitWrapper = styled.div`
border-radius:8px;
display:flex;
justify-content:center;
`
const LoginButton = styled.button`
cursor:pointer;
padding:1rem 3rem;
margin:0.5rem auto;
background:green;
border-radius:8px;
width:100%;
`