import React from "react";

import styled from 'styled-components'


class Video extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            isPlaying: false
        }
    }

    componentWillUnmount = () => {
        this.pauseVideo();
    };


    playVideo = () => {
        this.refs.vidRef.play();
        this.setState({
            isPlaying: true
        })
    };

    pauseVideo = () => {
        this.refs.vidRef.pause();
        this.setState({
            isPlaying: false
        })
    };

    render = () => {
        return (
            <>
                <VideoWrapper>
                    <video
                        ref="vidRef"
                        src={this.props.src}
                        type="video/mp4"
                    />
                </VideoWrapper>

                <Controls>
                    {this.state.isPlaying ? (
                        <TrailerButton onClick={this.pauseVideo}>
                            <img src='/static/images/play-icon-white.png' />
                            <span>Pause!</span>
                        </TrailerButton>
                    ) : (
                        <PlayButton onClick={this.playVideo}>
                            <img src='/static/images/play-icon-black.png' />
                            <span>Play!</span>
                        </PlayButton>
                    )}



                    <AddButton>
                        <span>+</span>
                    </AddButton>
                    <GroupWatchButton>
                        <img src='/static/images/group-icon.png' />
                    </GroupWatchButton>
                </Controls>
            </>

        );
    };
}

export default Video;

const AddButton = styled.button`
                width:44px;
                height:44px;
                display:flex;
                align-items:center;
                justify-content:center;
                border-radius:50%;
                border:white;
                background-color:rgb(0,0,0,0.6);
                color:white;
                cursor:pointer;
                margin-right:16px;
                span{
                    font - size:30px;
}
                `

const PlayButton = styled.button`
                border-radius:4px;
                font-size:15px;
                display:flex;
                align-items:center;
                height: 56px;
                background-color:rgb(249,249,249);
                padding: 0px 24px;
                border:none;
                letter-spacing:1.8px;
                cursor:pointer;
                margin-right:22px;
                text-transform:uppercase;

                &:hover{
                    background:rgb(198, 198, 198);
}
                `


const TrailerButton = styled(PlayButton)`
                background:rgba(0,0,0,0.3);
                border: 1px solid rgb(249,249,249);
                color: rgb(249,249,249);
                `
const GroupWatchButton = styled(AddButton)`
                background:rgb(0,0,0)
                `
const Controls = styled.div`
                display:flex;
                align-items:center;
                margin:2rem 0rem;
                `


const VideoWrapper = styled.div`
                height: 30vh;
                width: 30vw;
                min-height: 170px;
                min-width: 200px;
                margin-top:40px;
                border-radius:10px;
                margin:1rem 0rem;
                video{
                    width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius:10px;
                box-shadow:rgb(0 0 0 / 69%) 0px 26px 30px -10px,rgb(0 0 0 / 73%) 0px 16px 10px -10px;
                transition: all 250ms cubic-bezier(0.25,0.46,0.45,0.94) 0s;

                &:hover{
                    box - shadow:rgb(0 0 0 / 80%) 0px 40px 58px -16px,rgb(0 0 0 / 72%) 0px 30px 22px -10px;
                border-color:rgba(249, 249, 249, 0.8);
    }

}
                `





